body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.reload{
  position: absolute; 
  right:15px;
  top:15px;
  height: 6vmin;
  -webkit-animation: logo-spin infinite 10s linear;
          animation: logo-spin infinite 10s linear;
  cursor:pointer;
}

.reload:hover{
  height: 7vmin;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
.clock{
  position: absolute; 
  top: 15px;
  left: 15px;
  font-size: 330%;
  font-weight: bold;
  color: white;
}

.credits{
  position: absolute; 
  right:15px;
  bottom:15px;
  font-size: xx-small;
  color: white;
  background:rgba(0,0,0,0.7);
  padding: .2em .4em;
}

.credits a {
  color: white;
  text-decoration: none;
  font-size: xx-small;
}
.credits a:hover {
font-weight: bold;}

.img {
  height:100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

input {margin: .5em}

.memolist{
    min-height: 100px;
    width: 300px;
    position: absolute;
    background:rgba(0,0,0,0.9);
    color: white;
    top: 30%;
    left: 50%;
    padding: .8em;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  

h3 {
    font-size: xx-large;
    margin-top:10px!important;
  }

input
{
 width: 95%;
 border: 1px white solid;
 background: none;
 box-shadow: none;
 border-radius: 0px;
 color:white;
 padding: .5em;
 margin: .5em 0;
}

button {
    width: 100%;
    height: 30px;
    padding: 0;
    font-size: 16px;
    color: white;
    text-align: center;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
    background: #454545;
    border: 0;
    border-bottom: 2px solid #2f2e2e;
    cursor: pointer;
    box-shadow: inset 0 -2px #2f2e2e;
}
li {
    text-align:left;
}
.qod{
  position: absolute; 
  bottom: 50px;
  left: 20%;
  font-size: 30px;
  color: white;
  width: 60%;
}


.qod p{
  margin-top: 10px;
  margin-right: 80px;
  font-size: 23px;
  text-align:right;
}

.container {
  width: 100%;
  height: 100%;
  position: absolute;    
  margin:auto;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background-color: #aaa;
}

